// SCROLL UP

const scrollBtn = document.querySelector('.btn-scroll-up');

window.onscroll = () => {
   if (window.scrollY > 700) {
      scrollBtn.classList.remove('hide')
   }else if (window.scrollY < 700) {
      scrollBtn.classList.add('hide')
   }
};

scrollBtn.onclick = () => { 
   window.scrollTo(0, 0);
};



// BURGER

const burger = document.querySelector('.burger');
const burgerLine = document.querySelector('.burger__line');
const header = document.querySelector('.header');
const menu = document.querySelector('.menu');
const menuItem = document.querySelectorAll('.nav__item')

burger.addEventListener('click', () => {
   burger.classList.toggle('active');
   menu.classList.toggle('active');
   header.classList.toggle('height');
});



// TABS

const tabsBtn = document.querySelectorAll('.tabs__nav-btn')
const tabsItem = document.querySelectorAll('.tabs__item__cards')

tabsBtn.forEach(onTabClick)

function onTabClick(item) {
   item.addEventListener('click', () => {
      let currentBtn = item
      let tabId = currentBtn.getAttribute('data-tab')
      let currentTab = document.querySelector(tabId)

      if (!currentBtn.classList.contains('active')) {
         tabsBtn.forEach((item) => {
            item.classList.remove('active')
         })

         tabsItem.forEach((item) => {
            item.classList.remove('active')
         })

         currentBtn.classList.add('active')
         currentTab.classList.add('active')
      }
   })
}

document.querySelector('.tabs__nav-btn').click();



// ACCORDION

const accordionContent = document.querySelectorAll('.accordion-content');

accordionContent.forEach((item, index) => {
   let title = item.querySelector('.accordion-content__title');
   title.addEventListener('click', () => {
      item.classList.toggle('active');

      let text = item.querySelector('.accordion-content__text');
      if(item.classList.contains('active')) {
         text.style.height = `${text.scrollHeight}px`;
      }else {
         text.style.height = '0px';
      }

      removeActive(index);
   })
});

function removeActive(index1) {
   accordionContent.forEach((item2, index2) => {
      if(index1 != index2) {
         item2.classList.remove('active');

         let des = item2.querySelector('.accordion-content__text');
         des.style.height = '0px'
      }
   })
}